import utils from './utils'

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const colors = ['#46cef0', '#62cbe0']
let ticker = 0;
let spawnRate = 1;

// Event Listeners
addEventListener('resize', () => {
  canvas.width = innerWidth
  canvas.height = innerHeight

  init()
})

// RainDrops
class RainDrop {
  constructor(x, y, radius, color) {
    this.x = x
    this.y = y
    this.radius = radius
    this.color = color
    this.velocity = {
      x: 0,
      y: 3,
    }
    this.friction = 0.8
    this.gravity = 1
    this.miniDropAmount = 4
  }

  draw() {
    c.beginPath()
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    c.fillStyle = this.color
    c.fill()
    c.closePath()
  }

  shatter() {
    this.radius = 0
    for (let i = 0; i < this.miniDropAmount; i++) {

      miniRainDrops.push(new MiniRainDrop(this.x, this.y, 2.5))
    }
  }

  update() {
    this.draw()

    // When ball hits bottom of screen
    if (this.y + this.radius + this.velocity.y / 2.5 > canvas.height) {
      this.velocity.y = -this.velocity.y * this.friction
      this.shatter();
    } else {
      this.velocity.y += this.gravity
    }

    this.x += this.velocity.x;
    this.y += this.velocity.y;
  }
}

class MiniRainDrop extends RainDrop {
  constructor(x, y, radius, color) {
    super(x, y, radius, color)
    this.velocity = {
      x: utils.randomIntFromRange(-5, 5),
      y: utils.randomIntFromRange(-15, 15),
    }
    this.friction = 0.9
    this.gravity = 0.8

    // time (frame) until disappear from scene
    this.timeToLive = 15
    this.maxTimeToLive = this.timeToLive
    this.opacity = 1
    this.color = utils.hexToRgb(colors[1])
  }

  draw() {
    c.beginPath()
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    c.fillStyle = `rgba(${this.color.r}, ${this.color.g}, ${this.color.b}, ${this.opacity})`
    c.fill()
    c.closePath()
  }

  update() {
    this.draw()

    // When ball hits bottom of screen
    if (this.y + this.radius + this.velocity.y > canvas.height) {
      this.velocity.y = -this.velocity.y * this.friction
    } else {
      this.velocity.y += this.gravity
    }

    this.x += this.velocity.x;
    this.y += this.velocity.y;
    this.timeToLive -= 1
    this.opacity = this.timeToLive / this.maxTimeToLive
  }
}

// Implementation
let rainDrops
let miniRainDrops
function init() {
  rainDrops = []
  miniRainDrops = []
}

// Animation Loop
function animate() {
  requestAnimationFrame(animate)
  c.clearRect(0, 0, canvas.width, canvas.height)

  rainDrops.forEach((rainDrop, index) => {
    rainDrop.update()
    if (rainDrop.radius <= 0) {
      rainDrops.splice(index, 1)
    }
  })

  miniRainDrops.forEach((miniRainDrop, index) => {
    miniRainDrop.update()
    if (miniRainDrop.timeToLive <= 0) {
      miniRainDrops.splice(index, 1)
    }
  })

  ticker++

  if (ticker % spawnRate === 0) {
    const radius = 4;
    const x = utils.randomIntFromRange(radius, canvas.width - 3)
    const rainDropsAmount = 2;
    for (let i = 0; i < rainDropsAmount; i++) {

      rainDrops.push(new RainDrop(x, -15, radius, colors[0]))
    }
  }
}

init()
animate()
